.developer-footer {
        display: flex;
        justify-content: end;
}

.developer-footer h4 {
        margin: 0;
        font-weight: 600;
        font-size: 16px;
}

.developer-block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 1rem;
        color: #555;
        font-weight: 800;
        border-radius: 16px;
}

.developer-links {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 1.5rem;
        filter: brightness(1.5);
}

.developer-links a {
        font-style: none;
        text-decoration: none;
        color: #888
}

.developer-links a .link-icon img {
        width:25px;
        height:25px;
        filter: invert(28%) sepia(0%) saturate(0%) hue-rotate(161deg) brightness(150%) contrast(95%);
        transition: all 0.2s ease;
}

.developer-links a:hover .link-icon img,
.developer-links a:active .link-icon img,
.developer-links a:focus .link-icon img {
        filter: invert(59%) sepia(4%) saturate(19%) hue-rotate(338deg) brightness(190%) contrast(87%);
}